import React, { useState, useEffect } from 'react';
import InfoTip from './InfoTip';

const fieldTips = {
  name: '',
  type: 'single ()  chain ()',
  cost: '',
  multiplier: '1.220%',
  condition: '',
  quota: '',
  group: '//',
  order: '',
  prerequisite: '',
};

const initialFormState = {
  name: '',
  type: 'single',
  cost: 0,
  multiplier: 1,
  condition: '',
  quota: '',
  group: '',
  order: '',
  prerequisite: '',
};

function EventEditor({ card, onSave, onCancel, onDelete, customTabs = [] }) {
  const [formData, setFormData] = useState({
    name: '',
    type: 'single',
    group: '',
    cost: 0,
    multiplier: 1.0,
    condition: '',
    quota: '',
    order: '',
    prerequisite: '',
    isCustom: false,
    customTab: ''
  });

  useEffect(() => {
    if (card) {
      setFormData({
        name: card.name || '',
        type: card.type || 'single',
        group: card.group || '',
        cost: card.cost || 0,
        multiplier: card.multiplier || 1.0,
        condition: card.condition || '',
        quota: card.quota || '',
        order: card.order || '',
        prerequisite: card.prerequisite || '',
        isCustom: card.isCustom || false,
        customTab: card.customTab || ''
      });
    }
  }, [card]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const eventData = {
      ...formData,
      id: card?.id || Date.now(),
      cost: parseInt(formData.cost) || 0,
      multiplier: parseFloat(formData.multiplier) || 1.0,
      quota: formData.quota ? parseInt(formData.quota) : null,
      order: formData.order ? parseInt(formData.order) : null,
      customTab: formData.customTab?.trim() || ''
    };
    onSave(eventData);
  };

  const handleChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      background: 'rgba(0, 0, 0, 0.5)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 1000
    }}>
      <div style={{
        background: 'white',
        borderRadius: 12,
        padding: 24,
        width: '90%',
        maxWidth: 500,
        maxHeight: '90vh',
        overflowY: 'auto',
        boxShadow: '0 4px 24px rgba(0, 0, 0, 0.2)'
      }}>
        <h2 style={{ marginTop: 0, marginBottom: 20, color: '#1976d2' }}>
          {card ? '' : ''}
        </h2>
        
        <form onSubmit={handleSubmit}>
          <div style={{ marginBottom: 16 }}>
            <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}> *</label>
            <input
              type="text"
              value={formData.name}
              onChange={(e) => handleChange('name', e.target.value)}
              style={{
                width: '100%',
                padding: 8,
                border: '1px solid #ddd',
                borderRadius: 4,
                fontSize: 14
              }}
              required
            />
          </div>

          <div style={{ marginBottom: 16 }}>
            <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}> *</label>
            <select
              value={formData.type}
              onChange={(e) => handleChange('type', e.target.value)}
              style={{
                width: '100%',
                padding: 8,
                border: '1px solid #ddd',
                borderRadius: 4,
                fontSize: 14
              }}
            >
              <option value="single"></option>
              <option value="chain"></option>
            </select>
          </div>

          <div style={{ marginBottom: 16 }}>
            <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}></label>
            <input
              type="text"
              value={formData.group}
              onChange={(e) => handleChange('group', e.target.value)}
              placeholder=""
              style={{
                width: '100%',
                padding: 8,
                border: '1px solid #ddd',
                borderRadius: 4,
                fontSize: 14
              }}
            />
          </div>

          <div style={{ display: 'flex', gap: 16, marginBottom: 16 }}>
            <div style={{ flex: 1 }}>
              <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}></label>
              <input
                type="number"
                value={formData.cost}
                onChange={(e) => handleChange('cost', e.target.value)}
                min="0"
                style={{
                  width: '100%',
                  padding: 8,
                  border: '1px solid #ddd',
                  borderRadius: 4,
                  fontSize: 14
                }}
              />
            </div>
            <div style={{ flex: 1 }}>
              <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}></label>
              <input
                type="number"
                value={formData.multiplier}
                onChange={(e) => handleChange('multiplier', e.target.value)}
                min="0.1"
                step="0.1"
                style={{
                  width: '100%',
                  padding: 8,
                  border: '1px solid #ddd',
                  borderRadius: 4,
                  fontSize: 14
                }}
              />
            </div>
          </div>

          {formData.type === 'chain' && (
            <div style={{ display: 'flex', gap: 16, marginBottom: 16 }}>
              <div style={{ flex: 1 }}>
                <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}></label>
                <input
                  type="number"
                  value={formData.order}
                  onChange={(e) => handleChange('order', e.target.value)}
                  min="1"
                  placeholder="1, 2, 3..."
                  style={{
                    width: '100%',
                    padding: 8,
                    border: '1px solid #ddd',
                    borderRadius: 4,
                    fontSize: 14
                  }}
                />
              </div>
              <div style={{ flex: 1 }}>
                <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}></label>
                <input
                  type="text"
                  value={formData.prerequisite}
                  onChange={(e) => handleChange('prerequisite', e.target.value)}
                  placeholder=""
                  style={{
                    width: '100%',
                    padding: 8,
                    border: '1px solid #ddd',
                    borderRadius: 4,
                    fontSize: 14
                  }}
                />
              </div>
            </div>
          )}

          <div style={{ marginBottom: 16 }}>
            <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}></label>
            <input
              type="number"
              value={formData.quota}
              onChange={(e) => handleChange('quota', e.target.value)}
              min="1"
              placeholder=""
              style={{
                width: '100%',
                padding: 8,
                border: '1px solid #ddd',
                borderRadius: 4,
                fontSize: 14
              }}
            />
          </div>

          <div style={{ marginBottom: 24 }}>
            <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}></label>
            <textarea
              value={formData.condition}
              onChange={(e) => handleChange('condition', e.target.value)}
              placeholder="2"
              style={{
                width: '100%',
                padding: 8,
                border: '1px solid #ddd',
                borderRadius: 4,
                fontSize: 14,
                minHeight: 60,
                resize: 'vertical'
              }}
            />
          </div>

          <div style={{ marginBottom: 16 }}>
            <label style={{ display: 'block', marginBottom: 4, fontWeight: 600 }}>Tab</label>
            <input
              list="customTabList"
              value={formData.customTab}
              onChange={e => handleChange('customTab', e.target.value)}
              placeholder="Tab"
              style={{
                width: '100%',
                padding: 8,
                border: '1px solid #ddd',
                borderRadius: 4,
                fontSize: 14
              }}
            />
            <datalist id="customTabList">
              {customTabs.map(tab => (
                <option value={tab} key={tab} />
              ))}
            </datalist>
            <p style={{ margin: '4px 0 0 0', color: '#666', fontSize: '0.9em' }}>
              TabTab
            </p>
          </div>

          <div style={{ display: 'flex', gap: 12, justifyContent: 'flex-end' }}>
            {card && onDelete && (
              <button
                type="button"
                onClick={() => onDelete(card.id)}
                style={{
                  padding: '8px 16px',
                  background: '#f44336',
                  color: 'white',
                  border: 'none',
                  borderRadius: 4,
                  cursor: 'pointer',
                  fontSize: 14
                }}
              >
                
              </button>
            )}
            <button
              type="button"
              onClick={onCancel}
              style={{
                padding: '8px 16px',
                background: '#9e9e9e',
                color: 'white',
                border: 'none',
                borderRadius: 4,
                cursor: 'pointer',
                fontSize: 14
              }}
            >
              
            </button>
            <button
              type="submit"
              style={{
                padding: '8px 16px',
                background: '#4caf50',
                color: 'white',
                border: 'none',
                borderRadius: 4,
                cursor: 'pointer',
                fontSize: 14
              }}
            >
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default EventEditor; 