// 
export async function updateSessionData(sessionId, gameData) {
  const body = `action=updateSessionData&sessionId=${encodeURIComponent(sessionId)}&gameData=${encodeURIComponent(JSON.stringify(gameData))}`;
  console.log('updateSessionData  URL:', API_URL);
  console.log('updateSessionData :', { sessionId, gameDataSize: JSON.stringify(gameData).length });
  
  try {
    const res = await fetch(API_URL, {
      method: "POST",
      headers: { "Content-Type": "application/x-www-form-urlencoded" },
      body
    });
    console.log('updateSessionData :', res.status, res.statusText);
    
    if (!res.ok) {
      throw new Error(`HTTP error! status: ${res.status}`);
    }
    
    const data = await res.json();
    console.log('updateSessionData :', data);
    return data;
  } catch (error) {
    console.error('updateSessionData :', error);
    throw error;
  }
}

// 
export async function listActiveSessions() {
  const url = `${API_URL}?action=listActiveSessions`;
  console.log('listActiveSessions  URL:', url);
  
  try {
    const res = await fetch(url);
    console.log('listActiveSessions :', res.status, res.statusText);
    
    if (!res.ok) {
      throw new Error('Network response was not ok');
    }
    
    const data = await res.json();
    console.log('listActiveSessions :', data);
    return data;
  } catch (e) {
    console.error('listActiveSessions :', e);
    return { status: 'error', message: e.message || 'Load failed' };
  }
}

