import React, { useState, useEffect } from 'react';
import { createSession, getSessionData, setAdmin, listActiveSessions } from './sessionApi';

const SessionManager = ({ onJoinSession, onClose, clientId }) => {
  const [loading, setLoading] = useState(false);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showJoinModal, setShowJoinModal] = useState(false);
  const [showSessionList, setShowSessionList] = useState(false);
  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [newSessionName, setNewSessionName] = useState('');
  const [sessionIdToJoin, setSessionIdToJoin] = useState('');
  const [activeSessions, setActiveSessions] = useState([]);
  const [selectedSession, setSelectedSession] = useState(null);
  const [adminPassword, setAdminPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');

  console.log('SessionManager render:', { showCreateModal, showJoinModal, showSessionList, newSessionName });

  // 
  const loadActiveSessions = async () => {
    setLoading(true);
    try {
      const result = await listActiveSessions();
      if (result.status === 'success') {
        setActiveSessions(result.sessions || []);
      } else {
        console.error(':', result.message);
      }
    } catch (error) {
      console.error(':', error);
    }
    setLoading(false);
  };

  //  - 
  const handleCreateSession = async () => {
    console.log('handleCreateSession ');
    console.log('newSessionName:', newSessionName);
    
    if (!newSessionName.trim()) {
      alert('');
      return;
    }

    console.log(' localStorage:', newSessionName.trim());
    
    // 
    //  localStorage 
    localStorage.setItem('pendingSessionName', newSessionName.trim());
    
    setShowCreateModal(false);
    setNewSessionName('');
    
    console.log(' onJoinSession');
    //  null 
    onJoinSession(null, null, null);
    if (typeof onClose === 'function') onClose();
    
    console.log('handleCreateSession ');
  };

  // ID
  const handleJoinSession = async (sessionId) => {
    if (!sessionId) {
      alert(' ID');
      return;
    }

    setLoading(true);
    try {
      const result = await getSessionData(sessionId);
      if (result.status === 'success') {
        onJoinSession(sessionId, result.gameData, result.adminId);
        setShowJoinModal(false);
        setShowCreateModal(false);
        if (typeof onClose === 'function') onClose();
      } else {
        alert(': ' + (result.message || ''));
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
    setLoading(false);
  };

  // 
  const handleSelectSession = (session) => {
    setSelectedSession(session);
    setShowPasswordModal(true);
    setAdminPassword('');
    setPasswordError('');
  };

  // 
  const handleJoinAsAdmin = async () => {
    if (!adminPassword.trim()) {
      setPasswordError('');
      return;
    }

    setLoading(true);
    try {
      // 
      const adminResult = await setAdmin(selectedSession.sessionId, clientId, adminPassword);
      if (adminResult.status === 'success') {
        // 
        const result = await getSessionData(selectedSession.sessionId);
        if (result.status === 'success') {
          onJoinSession(selectedSession.sessionId, result.gameData, clientId);
          setShowPasswordModal(false);
          setShowSessionList(false);
          if (typeof onClose === 'function') onClose();
        } else {
          setPasswordError('');
        }
      } else {
        setPasswordError(adminResult.message || '');
      }
    } catch (error) {
      console.error(':', error);
      setPasswordError('');
    }
    setLoading(false);
  };

  // 
  const handleJoinAsViewer = async () => {
    setLoading(true);
    try {
      const result = await getSessionData(selectedSession.sessionId);
      if (result.status === 'success') {
        onJoinSession(selectedSession.sessionId, result.gameData, ''); // 
        setShowPasswordModal(false);
        setShowSessionList(false);
        if (typeof onClose === 'function') onClose();
      } else {
        alert(': ' + (result.message || ''));
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
    setLoading(false);
  };

  // 
  const generateViewerUrl = (sessionId) => {
    const baseUrl = window.location.origin + window.location.pathname;
    const viewerUrl = `${baseUrl}?viewer=${sessionId}`;
    console.log(':', { baseUrl, sessionId, viewerUrl });
    return viewerUrl;
  };

  return (
    <div style={{ padding: '20px', maxWidth: '600px', margin: '0 auto', textAlign: 'center' }}>
      <h2 style={{ color: '#1976d2', marginBottom: '40px' }}> RPG </h2>
      
      <div style={{ display: 'flex', flexDirection: 'column', gap: '20px', alignItems: 'center' }}>
        <button
          onClick={() => {
            console.log('');
            setShowCreateModal(true);
          }}
          style={{
            padding: '20px 40px',
            background: '#4caf50',
            color: 'white',
            border: 'none',
            borderRadius: '8px',
            cursor: 'pointer',
            fontSize: '18px',
            fontWeight: '600',
            minWidth: '200px'
          }}
        >
           
        </button>
        
        <button
          onClick={() => {
            setShowSessionList(true);
            loadActiveSessions();
          }}
          style={{
            padding: '20px 40px',
            background: '#2196f3',
            color: 'white',
            border: 'none',
            borderRadius: '8px',
            cursor: 'pointer',
            fontSize: '18px',
            fontWeight: '600',
            minWidth: '200px'
          }}
        >
           
        </button>
        
        <button
          onClick={() => setShowJoinModal(true)}
          style={{
            padding: '20px 40px',
            background: '#ff9800',
            color: 'white',
            border: 'none',
            borderRadius: '8px',
            cursor: 'pointer',
            fontSize: '18px',
            fontWeight: '600',
            minWidth: '200px'
          }}
        >
           ID
        </button>
      </div>

      {/*  Modal */}
      {showCreateModal && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '30px',
            borderRadius: '8px',
            minWidth: '400px',
            maxWidth: '500px'
          }}>
            <h3 style={{ margin: '0 0 20px 0', color: '#1976d2' }}></h3>
            <input
              type="text"
              value={newSessionName}
              onChange={(e) => setNewSessionName(e.target.value)}
              placeholder=""
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ddd',
                borderRadius: '4px',
                fontSize: '16px',
                marginBottom: '20px'
              }}
            />
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'flex-end' }}>
              <button
                onClick={() => setShowCreateModal(false)}
                style={{
                  padding: '10px 20px',
                  background: '#666',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
              <button
                onClick={handleCreateSession}
                disabled={loading}
                style={{
                  padding: '10px 20px',
                  background: '#4caf50',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: loading ? 'not-allowed' : 'pointer'
                }}
              >
                {loading ? '...' : ''}
              </button>
            </div>
          </div>
        </div>
      )}

      {/*  Modal */}
      {showSessionList && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '30px',
            borderRadius: '8px',
            minWidth: '500px',
            maxWidth: '600px',
            maxHeight: '80vh',
            overflow: 'auto'
          }}>
            <h3 style={{ margin: '0 0 20px 0', color: '#1976d2' }}></h3>
            
            {loading ? (
              <div style={{ textAlign: 'center', padding: '20px' }}>...</div>
            ) : activeSessions.length === 0 ? (
              <div style={{ textAlign: 'center', padding: '20px', color: '#666' }}>
                
              </div>
            ) : (
              <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                {activeSessions.map((session) => (
                  <div
                    key={session.sessionId}
                    style={{
                      border: '1px solid #ddd',
                      borderRadius: '8px',
                      padding: '15px',
                      cursor: 'pointer',
                      transition: 'background-color 0.2s'
                    }}
                    onMouseEnter={(e) => e.target.style.backgroundColor = '#f5f5f5'}
                    onMouseLeave={(e) => e.target.style.backgroundColor = 'white'}
                    onClick={() => handleSelectSession(session)}
                  >
                    <div style={{ fontWeight: '600', marginBottom: '5px' }}>
                      {session.createdBy || ''}
                    </div>
                    <div style={{ fontSize: '14px', color: '#666' }}>
                      ID: {session.sessionId}
                    </div>
                    <div style={{ fontSize: '12px', color: '#999' }}>
                      : {new Date(session.createdTime).toLocaleString()}
                    </div>
                  </div>
                ))}
              </div>
            )}
            
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'flex-end', marginTop: '20px' }}>
              <button
                onClick={() => setShowSessionList(false)}
                style={{
                  padding: '10px 20px',
                  background: '#666',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
            </div>
          </div>
        </div>
      )}

      {/*  Modal */}
      {showPasswordModal && selectedSession && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '30px',
            borderRadius: '8px',
            minWidth: '400px',
            maxWidth: '500px'
          }}>
            <h3 style={{ margin: '0 0 20px 0', color: '#1976d2' }}>
              : {selectedSession.createdBy || ''}
            </h3>
            
            <div style={{ marginBottom: '20px' }}>
              <label style={{ display: 'block', marginBottom: '8px', fontWeight: '600' }}>
                :
              </label>
              <input
                type="password"
                value={adminPassword}
                onChange={(e) => setAdminPassword(e.target.value)}
                placeholder=""
                style={{
                  width: '100%',
                  padding: '10px',
                  border: '1px solid #ddd',
                  borderRadius: '4px',
                  fontSize: '16px'
                }}
              />
              {passwordError && (
                <div style={{ color: 'red', fontSize: '14px', marginTop: '5px' }}>
                  {passwordError}
                </div>
              )}
            </div>

            <div style={{ marginBottom: '20px', padding: '15px', background: '#f5f5f5', borderRadius: '4px' }}>
              <div style={{ fontSize: '14px', marginBottom: '10px', fontWeight: '600' }}>
                :
              </div>
              <div style={{ fontSize: '12px', color: '#666', wordBreak: 'break-all' }}>
                {generateViewerUrl(selectedSession.sessionId)}
              </div>
            </div>
            
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'space-between' }}>
              <button
                onClick={handleJoinAsViewer}
                disabled={loading}
                style={{
                  padding: '10px 20px',
                  background: '#ff9800',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: loading ? 'not-allowed' : 'pointer',
                  flex: 1
                }}
              >
                 
              </button>
              <button
                onClick={handleJoinAsAdmin}
                disabled={loading}
                style={{
                  padding: '10px 20px',
                  background: '#4caf50',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: loading ? 'not-allowed' : 'pointer',
                  flex: 1
                }}
              >
                {loading ? '...' : ' '}
              </button>
            </div>
            
            <div style={{ display: 'flex', justifyContent: 'center', marginTop: '15px' }}>
              <button
                onClick={() => setShowPasswordModal(false)}
                style={{
                  padding: '8px 16px',
                  background: '#666',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
            </div>
          </div>
        </div>
      )}

      {/*  ModalID */}
      {showJoinModal && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '30px',
            borderRadius: '8px',
            minWidth: '400px',
            maxWidth: '500px'
          }}>
            <h3 style={{ margin: '0 0 20px 0', color: '#1976d2' }}>ID</h3>
            <input
              type="text"
              value={sessionIdToJoin}
              onChange={(e) => setSessionIdToJoin(e.target.value)}
              placeholder=" ID"
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ddd',
                borderRadius: '4px',
                fontSize: '16px',
                marginBottom: '20px'
              }}
            />
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'flex-end' }}>
              <button
                onClick={() => setShowJoinModal(false)}
                style={{
                  padding: '10px 20px',
                  background: '#666',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
              <button
                onClick={() => handleJoinSession(sessionIdToJoin)}
                disabled={loading}
                style={{
                  padding: '10px 20px',
                  background: '#2196f3',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: loading ? 'not-allowed' : 'pointer'
                }}
              >
                {loading ? '...' : ''}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default SessionManager; 